<template>
  <canvas ref="canvas" id="canvas"></canvas>
</template>

<script>
import WalkingSpriteAnimator from "@/classes/WalkingSpriteAnimator";
import SnowFlakes from "@/classes/SnowFlakes";
import SceneRenderer from "@/classes/SceneRenderer";
import FireWorks from "@/classes/FireWorks";

export default {
  name: "Decorator",
  data() {
    return {
      currentDate: new Date(),
      scene: null
    };
  },
  methods: {
    //bonka cheems minigame
    resizeCanvas() {
      let { innerWidth,innerHeight } = window;
      this.$refs.canvas.style.width = `${innerWidth}px`;
      this.$refs.canvas.style.height = `${innerHeight}px`;
      this.$refs.canvas.width = innerWidth;
      this.$refs.canvas.height = innerHeight;
    },
    isDateNear(day, month, beforeDays) {
      let today = this.currentDate.getDate();
      return this.currentDate.getMonth() + 1 === month && today  >= (day - beforeDays) && today <= day;
    }
  },
  async mounted() {
    this.resizeCanvas();
    window.addEventListener("resize", () => this.resizeCanvas());
    this.scene = new SceneRenderer(this.$refs.canvas);
    //New Years'eve (31,12,3)
    //Halloween (31,10,7)
    //Christmas(25,12,14)
    
    //New Years'eve
    if (this.isDateNear(31, 12, 3)) {
      this.scene.addRenderer(new FireWorks(this.$refs.canvas, {
        image: "sprites/newyear/firework.png",
        frames: 3
      },
      {
        image: "sprites/newyear/explosion.png",
        frames: 4
      }));
    } 
    //Halloween
    else if (this.isDateNear(31, 10, 7)) {
      this.scene.addRenderer(new WalkingSpriteAnimator(this.$refs.canvas, [{
        image: "sprites/halloween/AirA.png",
        frames: 5
      },
      {
        image: "sprites/halloween/BatC.png",
        frames: 3
      },
      {
        image: "sprites/halloween/BatSmallD.png",
        frames: 6
      },
      {
        image: "sprites/halloween/pumpkin.png",
        frames: 1
      }]));
    }
    //Christmas
    else if (this.isDateNear(25, 12, 20)) {
      this.scene.addRenderer(new SnowFlakes(this.$refs.canvas, "sprites/christmas/snowflake.png"));
      this.scene.addRenderer(new WalkingSpriteAnimator(this.$refs.canvas, [{
          image: "sprites/christmas/santa.png",
          frames: 3
      }]));
    }
    this.scene.start();
  }
};
</script>

<style scoped>
#canvas {
  position: absolute;
  pointer-events: none;
  left: 0px;
	top: 0px;
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-crisp-edges;
  image-rendering: pixelated;
  image-rendering: crisp-edges;
}
</style>
