<template>
  <canvas id="cnv" ref="computerCanvas"></canvas>
</template>

<script>
import * as THREE from "three";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader.js";
import { MTLLoader } from "three/examples/jsm/loaders/MTLLoader.js";

export default {
  name: "Computer3D",
  data() {
    return {
      scene: null,
      camera: null,
      renderer: null,
      geometry: null,
      material: null,
      computerModel: null,
      lastTimestamp: 0
    };
  },
  mounted: function () {
    this.scene = new THREE.Scene();
    this.scene.background = null;
    const mtlLoader = new MTLLoader();
    mtlLoader.load("computer.mtl", (mtl) => {
        mtl.preload();
        const loader = new OBJLoader();
        loader.setMaterials(mtl);
        loader.load(
          "computer.obj",
        (object) => {
            this.scene.add(object);
            this.computerModel = object;
            object.position.setZ(25);
             object.rotation.x = 0.5;
            requestAnimationFrame(animate);
          },
          function () {},
          function (error) {
            console.error("An error occoured while loading the model");
            console.error(error);
          }
        );
    });
    this.camera = new THREE.PerspectiveCamera(
      75,
      this.$refs.computerCanvas.width / this.$refs.computerCanvas.height,
      0.1,
      1000
    );
    
    const pointLight = new THREE.PointLight(0xffffff);
    pointLight.position.set(6,20,60);
    this.scene.add(pointLight);

    this.renderer = new THREE.WebGLRenderer({
      canvas: this.$refs.computerCanvas,
      alpha: true,
    });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize( 300, 300);
    this.camera.position.setZ(30);

    let animate = (time) => {
      requestAnimationFrame(animate);
      let deltaTime = (time - this.lastTimestamp) / 1000;
      this.computerModel.rotation.y += deltaTime;
      this.renderer.render(this.scene, this.camera);
      const canvas = this.renderer.domElement;
      this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
      this.camera.updateProjectionMatrix();
      this.lastTimestamp = time;
    };
  }
};
</script>

<style scoped>
#cnv {
  width: 70%;
  height: 50%;
}
@media screen and (max-width: 800px) {
  #cnv {
    width: 90%;
    height: 50%;
  }
}
</style>
