import { randint } from "./utils";
import SpriteSplitter from "@/classes/SpriteSplitter";

export default class FireWorks {
    constructor(canvas, sprite, explosion) {
        this.canvas = canvas;
        this.ctx = canvas.getContext("2d");
        this.fireWorks = [];
        this.explosions = [];
        this.sprite = sprite;
        this.explosionSrc = explosion;
        this.fireWork = new Image();
        this.explosion = new Image();
        this.isRunning = false;
        this.animationIndex = 0;
        this.lastFrameChange = 0;
        this.deltaAnime = 80;
    }

    async start() {
        this.running = true;
        this.fireWork = await SpriteSplitter(this.sprite.image, this.sprite.frames, false);
        this.explosion = await SpriteSplitter(this.explosionSrc.image, this.explosionSrc.frames, false);
        this.addFireWork();
    }

    stop() {
        this.running = false;
    }

    addFireWork() {
        this.fireWorks.push({
            x: randint(20, this.canvas.width),
            y: randint(this.canvas.height + 10, this.canvas.height + 50),
            speed: randint(2, 5),
            activation: randint(30, this.canvas.height - 30)
        });
        setTimeout(() => this.addFireWork(), randint(800, 1200));
    }

    draw() {
        for (let i = 0; i < this.fireWorks.length; i++) {
            this.ctx.drawImage(this.fireWork[this.animationIndex], this.fireWorks[i].x, this.fireWorks[i].y, 32, 32);
        }
        for (let i = 0; i < this.explosions.length; i++) {
            this.ctx.drawImage(this.explosion[this.animationIndex], this.explosions[i].x, this.explosions[i].y, 32, 32);
        }
    }

    update(time) {
        let deltaTime = (time - this.lastUpdate) / 10;
        if ((time - this.lastFrameChange) >= this.deltaAnime) {
            this.animationIndex = ++this.animationIndex % this.fireWork.length;
            this.lastFrameChange = time;
            if (this.explosions.length !== 0) {
                this.explosions = this.explosions.filter((el) => {
                    el.i !== this.explosion.length - 1;
                });
                for (let i = 0; i < this.explosions.length; i++) {
                    this.explosions[i].i = ++this.explosions[i].i % this.explosion.length;
                }
            }
        }
        if (this.fireWorks.length !== 0) {
            for (let i = 0; i < this.fireWorks.length; i++) {
                this.fireWorks[i].y -= this.fireWorks[i].speed * deltaTime;
            }
        }
        this.fireWorks = this.fireWorks.filter((el) => {
            if (el.y > el.activation) {
                return true;
            } else {
                this.explosions.push({
                    x: el.x,
                    y: el.y,
                    i: 0
                });
                return false;
            }
        });
    }
}