import { loadImage, randint } from "./utils";

export default class SnowFlakes {
    constructor(canvas, image) {
        this.speed = 2;
        this.canvas = canvas;
        this.ctx = canvas.getContext("2d");
        this.imgSrc = image;
        this.image = new Image();
        this.snowFlakes = [];
        this.bottomMargin = 0;
    }

    async start() {
        this.image = await loadImage(this.imgSrc);
        this.addSnowFlake();
    }

    addSnowFlake() {
        this.snowFlakes.push({
            x: randint(20, this.canvas.width),
            y: randint(-20, -80),
            speed: randint(2, 4)
        });
        setTimeout(() => this.addSnowFlake(), randint(300, 600));
    }

    draw() {
        for (let i = 0; i < this.snowFlakes.length; i++) {
            this.ctx.drawImage(this.image, this.snowFlakes[i].x, this.snowFlakes[i].y, 24, 24);
        }
    }

    update(time) {
        let deltaTime = (time - this.lastUpdate) / 10;
        if (this.snowFlakes.length !== 0) {
            for (let i = 0; i < this.snowFlakes.length; i++) {
                this.snowFlakes[i].y += this.snowFlakes[i].speed * deltaTime;
            }
        }
        this.snowFlakes = this.snowFlakes.filter((el) => {
            return (el.y < this.canvas.height + 20);
        });
    }
}