import SpriteSplitter from "@/classes/SpriteSplitter.js";
import { randint } from "./utils";

export default class WalkingSpriteAnimator {
    constructor(canvas, images) {
        this.speed = 2.7;
        this.directions = {
            RIGHT: 1,
            LEFT: 2
        };
        this.deltaAnime = 100;
        this.maxAnimationInterval = 6;
        this.canvas = canvas;
        this.ctx = canvas.getContext("2d");
        this.images = images;
        this.sprites = [];
        this.lastUpdate = 0;
        this.animationIndex = 0;
        this.currentAnimation = null;
        this.posX = 0;
        this.currentDirection = this.directions.LEFT;
        this.lastFrameChange = 0;
        this.bottomMargin = 0;
    }

    async start() {
        for (let i = 0; i < this.images.length; i++) {
            this.sprites.push(await SpriteSplitter(this.images[i].image, this.images[i].frames, false));
        }
        this.nextAnimation();
    }

    chooseRandomAnimation() {
        this.bottomMargin = randint(10, 30);
        this.currentDirection = this.directions[this.getRandomObjectKey(this.directions)];
        if (this.currentDirection === this.directions.RIGHT) {
            this.posX = -10;
        } else if (this.currentDirection === this.directions.LEFT) {
            this.posX = this.canvas.width + 10;
        }
        this.animationIndex = 0;
        this.currentAnimation = this.sprites[Math.floor(Math.random() * this.sprites.length)];
    }

    nextAnimation() {
        this.currentAnimation = null;
        setTimeout(() => {
            this.chooseRandomAnimation();
        }, randint(3, 6) * 1000);
    }

    isDefined(obj) {
        return obj !== null && obj !== undefined;
    }

    getRandomObjectKey(obj) {
        return Object.keys(obj)[Math.floor(Math.random() * Object.keys(obj).length)];
    }

    draw() {
        let width, height = 0;
        if (this.isDefined(this.currentAnimation)) {
            if (window.innerWidth > window.innerHeight) {
                width = window.innerWidth / 20;
                height = (this.currentAnimation[this.animationIndex].height / this.currentAnimation[this.animationIndex].width) * width;
            } else {
                height = window.innerHeight / 20;
                width = (this.currentAnimation[this.animationIndex].width / this.currentAnimation[this.animationIndex].height) * height;
            }
            if (this.currentDirection === this.directions.RIGHT) {
                this.ctx.save();
                this.ctx.scale(-1, 1);
                this.ctx.drawImage(this.currentAnimation[this.animationIndex], -this.posX, (this.canvas.height - height) - this.bottomMargin, -width, height);
                this.ctx.restore();
            } else {
                this.ctx.drawImage(this.currentAnimation[this.animationIndex], this.posX, (this.canvas.height - height) - this.bottomMargin, width, height);
            }
        }
    }

    update(time) {
        if (this.isDefined(this.currentAnimation)) {
            let deltaTime = (time - this.lastUpdate) / 10;
            let widthFactor = window.innerWidth / 1000;
            if ((time - this.lastFrameChange) >= this.deltaAnime) {
                this.animationIndex = ++this.animationIndex % this.currentAnimation.length;
                this.lastFrameChange = time;
            }
            if (this.currentDirection === this.directions.RIGHT) {
                this.posX += this.speed * deltaTime * widthFactor;
                if (this.posX + 30 >= this.canvas.width) {
                    this.nextAnimation();
                }
            } else if (this.currentDirection === this.directions.LEFT) {
                this.posX -= this.speed * deltaTime * widthFactor;
                if (this.posX <= -30) {
                    this.nextAnimation();
                }
            }
        }
    }
}